import * as R from 'ramda';

export const getToken = state => state.auth.token;

export const getItemById = (state, id) => {
  const item = R.prop(id, state.items);
  return item;
}

export const getOneItemById = (state, id) => {
  const item = R.prop(id, state.items);
if(item){
  if (Object.keys(state.locations).length > 0) {
    for (let key in state.locations) {
      if (state.locations[key].id === item.locationId) {
        item.location = state.locations[key].name
      }
    }
  }

  if (Object.keys(state.district).length > 0) {
    for (let key in state.district) {
      if (state.district[key].id === item.districtId) {
        item.district = state.district[key].name
      }
    }
  }

  if (Object.keys(state.realtObject).length > 0) {
    for (let key in state.realtObject) {
      if (state.realtObject[key].id === item.realtObject) {
        item.realtObject = state.realtObject[key].name
      }
    }
  }

  if (Object.keys(state.categories).length > 0) {
    for (let key in state.categories) {
      if (state.categories[key].id === item.categoryId) {
        item.category = state.categories[key].name
      }
    }
  }
}
  return item;
}

export const getActiveCategoryId = state => state.activeCategory.id
export const getActiveLocationId = state => state.activeLocation.id
export const getActiveDistrictId = state => state.activeDistrict.id
export const getActiveRealtObjectId = state => state.activeRealtObject.id
export const getActiveDeal = state => state.activeDeal

export const getActiveCategory = state => state.activeCategory
export const getActiveLocations = state => state.activeLocation
export const getActiveDistrict = state => state.activeDistrict
export const getActiveRealtObject = state => state.activeRealtObject


export const getItems = state => {

  const activeCategoryId = getActiveCategoryId(state);
  const activeLocationId = getActiveLocationId(state);
  const activeDistrictId = getActiveDistrictId(state);
  const activeRealtObjectId = getActiveRealtObjectId(state);

  const applySearch = item =>
    R.contains(
      state.itemsPage.search.toLowerCase(),
      R.prop('name', item).toLowerCase()
    );

  const applyPrice = item => (state.priceRange.min <= R.prop('price', item)) && (R.prop('price', item) <= state.priceRange.max);

  const appyCategory = item =>
    R.equals(getActiveCategoryId(state), R.prop('categoryId', item));

  const appyLocation = item =>
    R.equals(getActiveLocationId(state), R.prop('locationId', item));

  const appyDistrict = item =>
    R.equals(getActiveDistrict(state), R.prop('districtId', item));

  const appyrealtObject = item =>
    R.equals(getActiveRealtObject(state), R.prop('realtObjectId', item));

  const appyDeal = item =>
    R.equals(getActiveDeal(state), R.prop('sale', item));

  return R.compose(
    R.filter(applyPrice),
    R.filter(applySearch),
    R.when(R.always(activeCategoryId), R.filter(appyCategory)),
    R.when(R.always(activeLocationId), R.filter(appyLocation)),
    R.when(R.always(activeDistrictId), R.filter(appyDistrict)),
    R.when(R.always(activeRealtObjectId), R.filter(appyrealtObject)),
    R.when(R.always(getActiveDeal), R.filter(appyDeal)),
    R.map(id => getItemById(state, id))
  )(state.itemsPage.ids);
};

export const getRenderedItemsLength = (state, activeCategoryId) => {
  const applySearch = item =>
    R.contains(
      state.itemsPage.search.toLowerCase(),
      R.prop('name', item).toLowerCase()
    );

  const appyCategory = item =>
    R.equals(activeCategoryId, R.prop('categoryId', item));

  const items = R.compose(
    R.filter(applySearch),
    R.when(R.always(activeCategoryId), R.filter(appyCategory)),
    R.map(id => getItemById(state, id))
  )(state.itemsPage.ids);

  return R.length(items);
};


export const getCategories = state => {
  const arr = R.values(state.categories);
  arr.sort((a, b) => {
    if (a.name.toLowerCase() === 'другое') {
      return 1;
    }
    return a.name.localeCompare(b.name);
  });

  return arr;
};
