export const getQuestions = state => R.values(state.questions);

export const getFiles = state => R.values(state.files);
export const getcheckItems = state => {
  const items = [];
  for (let key in state.checkitems.checkitems) {
    items.push(state.checkitems.checkitems[key]);
  }
  return items;
};



