import React, { Component } from 'react';
import { connect } from 'react-redux';
import OneCheckItem from './OneCheckItem';
import { fetchItemsCheck } from './../../actions';
import { getcheckItems, getToken } from './../../selectors';
import EditCategory from '../editCategory';
import EditLocations from '../editLocations';
import EditDistrict from '../editDistrict';
import EditRealtObject from '../editRealtObject';
import EditQuestions from '../questions';


class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: 'check'
    }
  }

  handleTogleMenu = e => {
    this.setState({ active: e.target.id })
  }

  componentDidMount() {
    this.props.fetchItemsCheck(this.props.token);

  }

  render() {
    const { items } = this.props;
    const { active } = this.state;
    return (
      <div className="container mt-5">
        <div className="row d-flex justify-content-center">
          <div className="col-12 col-sm-12 col-md-10 col-lg-2">
            <ul className="list-group admin-menu">
              <li
                className={
                  active === 'check'
                    ? 'list-group-item active'
                    : 'list-group-item'
                }
                id="check"
                onClick={this.handleTogleMenu}
              >
                Новые объявления
              </li>
              <li
                className={
                  active === 'category'
                    ? 'list-group-item active'
                    : 'list-group-item'
                }
                id="category"
                onClick={this.handleTogleMenu}
              >
                Категории
              </li>
              <li
                className={
                  active === 'location'
                    ? 'list-group-item active'
                    : 'list-group-item'
                }
                id="location"
                onClick={this.handleTogleMenu}
              >
                Населенные пункты
              </li>
              <li
                className={
                  active === 'district'
                    ? 'list-group-item active'
                    : 'list-group-item'
                }
                id="district"
                onClick={this.handleTogleMenu}
              >
                Районы
              </li>
              <li
                className={
                  active === 'realtObject'
                    ? 'list-group-item active'
                    : 'list-group-item'
                }
                id="realtObject"
                onClick={this.handleTogleMenu}
              >
                Объекты недвижимости
              </li>
              <li
                className={
                  active === 'questions'
                    ? 'list-group-item active'
                    : 'list-group-item'
                }
                id="questions"
                onClick={this.handleTogleMenu}
              >
                Вопросы
              </li>
            </ul>
          </div>
          <div className="col-md-10">
            {active === 'check' ? (
              <div className="books row pt-2 animated slideInRight">
                {items.length === 0 ? (
                  <p className="ml-5 mt-3">Новых Объявлений нет</p>
                ) : null}
                {items.map((item, index) => (
                  <OneCheckItem item={item} key={index} />
                ))}
              </div>
            ) : null}
            {active === 'category' ? (
              <div className="row">
                <div className="col-md-12 animated slideInRight">
                  <EditCategory />
                </div>
              </div>
            ) : null}
            {active === 'location' ? (
              <div className="row">
                <div className="col-md-12 animated slideInRight">
                  <EditLocations />
                </div>
              </div>
            ) : null}
            {active === 'district' ? (
              <div className="row">
                <div className="col-md-12 animated slideInRight">
                  <EditDistrict />
                </div>
              </div>
            ) : null}
            {active === 'realtObject' ? (
              <div className="row">
                <div className="col-md-12 animated slideInRight">
                  <EditRealtObject />
                </div>
              </div>
            ) : null}
            {active === 'questions' ? (
              <div className="row">
                <div className="col-md-12 animated slideInRight">
                  <EditQuestions />
                </div>
              </div>
            ) : null}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    items: getcheckItems(state, ownProps),
    token: getToken(state),
  };
};

const mapDispatchToProps = {
  fetchItemsCheck,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Admin);
