import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropsItem from './questionsList';
import { deleteQuestions, fetchQuestions } from '../../actions';
import { getToken, getQuestions } from '../../selectors';

class EditQuestions extends PureComponent {
  static getDerivedStateFromProps({ items }, { prevOriginalItems }) {
    if (items !== prevOriginalItems) {
      return {
        prevOriginalItems: items,
        items,
      };
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { items } = this.props;

    this.state = {
      items: items,
      prevOriginalItems: items,
		};
		
    this.onDelete = this.onDelete.bind(this);
  }

  onDelete(id) {
    const { token } = this.props;
    const { items } = this.state;

    this.props.deleteQuestions(id, token);

    let newItems = items.slice();
    newItems.splice(newItems.findIndex(e => e.id === id), 1);
    this.setState({ items: newItems });
  }

  componentDidMount() {
    const { items } = this.props;

    if (items && items.length === 0) {
      this.props.fetchQuestions(this.props.token);
    }
  }

  render() {
    const { items } = this.state;

    return (
      <div className="d-flex flex-column justify-content-center mt-4">
        <div className="col-md-12">
          <h2>Вопросы</h2>
          {items &&
            items.map(item => {
              return (
                <PropsItem
                  key={item.id}
                  item={item}
                  onDelete={this.onDelete}
                />
              );
            })}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    items: getQuestions(state),
    token: getToken(state),
  };
};
const mapDispatchToProps = {
  deleteQuestions,
  fetchQuestions,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EditQuestions);
