import React from 'react';
import { connect } from 'react-redux';
import { getActiveDeal } from './../../selectors';
import { changeActiveDeal } from '../../actions';


class Deal extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      deal: false
    }
  }

  handleToggleSaleTrue = () => {
    this.setState({ deal: true })
    this.props.changeActiveDeal(true)
  }
  handleToggleSaleFalse = () => {
    this.setState({ deal: false })
    this.props.changeActiveDeal(false)
  }



  render() {

    return (
      <div className="well">
        <div className="btn-group btn-group-sm btn-group-toggle" data-toggle="buttons">
          <label className={this.props.activeDeal ? 'btn btn-outline-secondary active' : 'btn btn-outline-secondary'} onClick={this.handleToggleSaleTrue}>
            <input type="radio" name="options" id="option1" /> Продажа
                      </label>
          <label className={this.props.activeDeal ? 'btn btn-outline-secondary' : 'btn btn-outline-secondary active'} onClick={this.handleToggleSaleFalse}>
            <input type="radio" name="options" id="option2" /> Аренда
                      </label>
        </div>
      </div>

    );
  }
};

const mapStateToProps = (state) => ({
  activeDeal: getActiveDeal(state),
});

const initMapDispatchToProps = {
  changeActiveDeal
};

export default connect(
  mapStateToProps,
  initMapDispatchToProps
)(Deal);
