import * as R from 'ramda';

import {
  FETCH_LOCATION_SUCCESS,
  ADD_LOCATION_SUCCESS,
  DEL_LOCATION_BY_ID_SUCCESS,
} from './../actionTypes';

const initialState = {};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case FETCH_LOCATION_SUCCESS: {
      const newValues = R.indexBy(R.prop('id'), payload);
      return R.merge(state, newValues);
    }
    case ADD_LOCATION_SUCCESS: {
      const newValues2 = R.indexBy(R.prop('id'), [payload]);
      return R.merge(state, newValues2);
    }
    case DEL_LOCATION_BY_ID_SUCCESS: {
      const newValue3 = {}
      for (let key in state) {
        if (key !== payload) {
          newValue3[key] = state[key];
        }
      }
      return newValue3;
    }
    default:
      return state;
  }
};
