import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changePriceRange } from './../../../actions';

class RangePrice extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value1: 0,
      value2: 20000000,
    };

  }

  handleOnChange1 = e => {
    this.setState({ value1: e.target.value });
    this.handleStartFiltr();
  };
  handleOnChange2 = e => {
    this.setState({ value2: e.target.value });
    this.handleStartFiltr();
  };

  changePriceRange = () => {
    const { value1, value2 } = this.state;
    let price = { min: value1, max: value2 };
    this.props.changePriceRange(price);
  };

  handleStartFiltr = () => {
    clearTimeout(this.timerID);
    this.timerID = setTimeout(this.changePriceRange, 1000);
  };

  render() {
    const { value1, value2 } = this.state;
    return (
      <div>
        <form>
          <span className='text-price'>Цена:</span>
          <div className="form-group">
            <div className="input-group input-group-sm ">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="inputGroup-sizing-sm"
                >
                  от
                </span>
              </div>
              <input
                className="form-control form-control-sm"
                type="number"
                value={value1}
                onChange={this.handleOnChange1}
              />
            </div>
            <input
              type="range"
              className="custom-range mb-4"
              id="customRange1"
              min="0"
              max="20000000"
              step="1000"
              value={value1}
              onMouseUp={this.handleStartFiltr}
              onChange={this.handleOnChange1}
            />
            <div className="input-group input-group-sm">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="inputGroup-sizing-sm"
                >
                  до
                </span>
              </div>
              <input
                className="form-control form-control-sm"
                type="number"
                value={value2}
                onChange={this.handleOnChange2}
              />
            </div>
            <input
              type="range"
              className="custom-range"
              id="customRange2"
              min="0"
              max="20000000"
              step="1000"
              value={value2}
              onChange={this.handleOnChange2}
              onMouseUp={this.handleStartFiltr}
            />
          </div>
        </form>
      </div>
    );
  }
}

const mapDispatchToProps = {
  changePriceRange,
};

export default connect(
  null,
  mapDispatchToProps
)(RangePrice);
