import React, { Component } from 'react';
import {
  getItemById,
  getCategories,
  getToken,
  getFiles,
  getLocations,
  getDistrict,
  getRealtObject
} from './../../selectors';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import {
  fetchCategories,
  patchonServer,
  fetchItemByID,
  delItem,
  uploadFile,
  DeleteImgFile,
  DeleteAllImgFile,
  fetchLocations,
  fetchDistrict,
  fetchRealtObject
} from './../../actions';
import Gallery from 'react-grid-gallery';
import InputMask from 'react-input-mask';
import Switch from '@material-ui/core/Switch';

class EditItem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      first: true,
      id: this.props.params.id,
      files: [],
      images: [],
      name: '',
      description: '',
      price: '',
      categoriesid: '',
      locations: '',
      district: '',
      realtObject: '',
      area: 0,
      floor: 0,
      sale: true,
      tel: '',
      email: '',
      checked: [],
      spinner: false
    };

    this.patchonServer = this.props.patchonServer.bind(this);
    this.delItem = this.props.delItem.bind(this);
  }

  static getDerivedStateFromProps(props, state) {
    if (state.first && props.item !== undefined) {
      let st = [];
      if (props.item.status) {
        st = ['statusTrue'];
      }

      const img = props.item.images.map(src => {
        return {
          src: src,
          thumbnail: src,
          thumbnailWidth: 320,
          thumbnailHeight: 174,
          isSelected: false,
          caption: '',
        };
      });

      return {
        name: props.item.name,
        description: props.item.description,
        price: props.item.price,
        categoriesid: props.item.categoryId,
        tel: props.item.tel,
        email: props.item.email,
        checked: st,
        first: false,
        images: img,
      };
    }

    if (!state.first && props.files.length !== state.images.length) {
      const currentsrc = state.images.map(el => el.src);

      const newFile = props.files.filter(
        newSrc => currentsrc.indexOf(newSrc) === -1
      );

      if (newFile && newFile.length > 0) {
        const img = newFile.map(el => {
          return {
            src: el,
            thumbnail: el,
            thumbnailWidth: 320,
            thumbnailHeight: 174,
            isSelected: false,
            caption: '',
          };
        });

        return { images: state.images.concat(img), spinner: false };
      }
    }

    return null;
  }

  componentDidMount() {
    this.props.fetchItemByID(this.props.params.id);
    this.props.fetchCategories();
    this.props.fetchLocations();
    this.props.fetchDistrict();
    this.props.fetchRealtObject();
  }

  allImagesSelected = images => {
    var f = images.filter(function (img) {
      return img.isSelected === true;
    });
    return f.length === images.length;
  };

  onSelectImage = index => {
    var images = this.state.images.slice();
    var img = images[index];
    if (img.hasOwnProperty('isSelected')) img.isSelected = !img.isSelected;
    else img.isSelected = true;

    this.setState({
      images: images,
    });

    if (this.allImagesSelected(images)) {
      this.setState({
        selectAllChecked: true,
      });
    } else {
      this.setState({
        selectAllChecked: false,
      });
    }
  };

  handleImageChange = e => {
    this.setState({ spinner: true })
    e.preventDefault();
    let reader = new FileReader();
    let file = e.target.files;
    for (let i = 0; i < file.length; i++) {
      if (file[i]) {
        reader.onloadend = () => {
          this.props.uploadFile(file[i]);
        };
        reader.readAsDataURL(file[i]);
      }
    }
  };

  getSelectedImages() {
    const selected = [];
    for (let i = 0; i < this.state.images.length; i++) {
      if (this.state.images[i].isSelected === true) selected.push(i);
    }
    return selected;
  }

  handleImageDell = () => {
    const selectIndex = this.getSelectedImages();
    this.props.DeleteImgFile(selectIndex);
    this.setState({
      images: this.state.images.filter(
        (el, i) => selectIndex.indexOf(i) === -1
      ),
    });
  };

  handleChangeName = e => {
    this.setState({ name: e.target.value });
  };

  handleChangeDescription = e => {
    this.setState({ description: e.target.value });
  };

  handleChangePrice = e => {
    this.setState({ price: e.target.value });
  };

  handleChangeCategories = e => {
    this.setState({ categoriesid: e.target.value });
  };

  handleChangeEmail = e => {
    this.setState({ email: e.target.value });
  };

  handleSubmit = e => {
    const nItem = {
      id: this.props.params.id,
      name: this.state.name,
      description: this.state.description,
      price: this.state.price,
      categoryId: this.state.categoriesid,
      email: this.state.email,
      tel: this.state.tel,
      status: this.state.checked.indexOf('statusTrue') !== -1,
      images: this.state.images.map(el => el.src),
      locationId: this.state.locations !== '' ? this.state.locations.id : this.props.locations[0].id,
      districtId: this.state.district !== '' ? this.state.district.id : this.props.district[0].id,
      realtObjectId: this.state.realtObject !== '' ? this.state.realtObject.id : this.props.realtObject[0].id,
      area: this.state.area,
      floor: this.state.floor,
      sale: this.state.sale,
    };
    this.patchonServer(nItem, this.props.token);
    this.props.DeleteAllImgFile();
    e.preventDefault();
    browserHistory.push('/admin');
  };

  handleDellItem = () => {
    if (window.confirm('Удалить объявление?')) {
      this.delItem(this.state.id, this.props.token);
      browserHistory.push('/admin');
    }
  };

  handleToggle = value => () => {
    const { checked } = this.state;
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    this.setState({
      checked: newChecked,
    });
  };

  handleChangeTel = e => {
    this.setState({ tel: e.target.value });
  };


  handleToggleSaleTrue = () => {
    this.setState({ sale: true })
  }
  handleToggleSaleFalse = () => {
    this.setState({ sale: false })
  }

  handleChangeLocation = e => {
    this.setState({ locations: e.target.value });
  };

  handleChangeDistrict = e => {
    this.setState({ district: e.target.value });
  };

  handleChangeRealtObject = e => {
    this.setState({ realtObject: e.target.value });
  };


  render() {
    const {
      name,
      description,
      price,
      categoriesid,
      images,
      email,
      tel,
      spinner
    } = this.state;

    return (
      <div className="view-container mt-5 pt-5 text-center">
        <div className="container">
          <div className="row">
            <h1>Редактирование объвления</h1>
            <form className="col-lg-12" onSubmit={this.handleSubmit}>
              <div className="form-group row">
                <div className="col-md-6">
                  <h5>Фото:</h5>
                  <div className="btn-group">
                    <label className="btn btn-outline-success ">
                      Добавить
                      {spinner && <img src="https://loading.io/spinners/rolling/lg.curve-bars-loading-indicator.gif" id="spinner" alt="..." />}
                      <input
                        className="fileInput"
                        name="file"
                        type="file"
                        accept="image/*,image/jpeg"
                        onChange={this.handleImageChange}
                      />
                    </label>
                    <label className="btn btn-outline-warning">
                      Удавить выбранные
                      <input
                        className="fileDel"
                        type="button"
                        onClick={this.handleImageDell}
                      />
                    </label>
                  </div>
                  <div className="previewComponent">
                    {Array.isArray(images) && images.length !== 0 && (
                      <Gallery
                        images={images}
                        enableImageSelection={true}
                        onSelectImage={this.onSelectImage}
                      />
                    )}
                  </div>
                </div>

                <div className="col-md-6">
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Название</label>
                    <div className="col-sm-9">
                      <input
                        type="text"
                        className="form-control"
                        onChange={this.handleChangeName}
                        id="inputName"
                        value={name}
                      />
                    </div>
                  </div>

                  <div className="form-group row mt-2 d-flex justify-content-center">
                    <div className="form-group has-success ">
                      <div className="btn-group btn-group-toggle" data-toggle="buttons">
                        <label className={this.state.sale ? 'btn btn-outline-secondary active' : 'btn btn-outline-secondary'} onClick={this.handleToggleSaleTrue}>
                          <input type="radio" name="options" id="option1" /> Продажа
                      </label>
                        <label className={this.state.sale ? 'btn btn-outline-secondary' : 'btn btn-outline-secondary active'} onClick={this.handleToggleSaleFalse}>
                          <input type="radio" name="options" id="option2" /> Аренда
                      </label>
                      </div>
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Населенный пункт</label>
                    <div className="col-sm-9">
                      <select
                        className="form-control"
                        onChange={this.handleChangeLocation}
                        value={this.state.locations}
                        id="select"
                      >
                        {this.props.locations.map(data => (
                          <option key={data.id} value={data.id}>
                            {data.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Район</label>
                    <div className="col-sm-9">
                      <select
                        className="form-control"
                        onChange={this.handleChangeDistrict}
                        value={this.state.district}
                        id="select"
                      >
                        {this.props.district.map(data => (
                          <option key={data.id} value={data.id}>
                            {data.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Объект недвижимости</label>
                    <div className="col-sm-9">
                      <select
                        className="form-control"
                        onChange={this.handleChangeRealtObject}
                        value={this.state.realtObject}
                        id="select"
                      >
                        {this.props.realtObject.map(data => (
                          <option key={data.id} value={data.id}>
                            {data.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Категория</label>
                    <div className="col-sm-9">
                      <select
                        className="form-control"
                        onChange={this.handleChangeCategories}
                        value={categoriesid}
                      >
                        {this.props.categores.map(data => (
                          <option key={data.id} value={data.id}>
                            {data.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Площадь:</label>
                    <div className="col-sm-9">
                      <input
                        type="number"
                        className="form-control"
                        id="inputPrice"
                        onChange={this.handleChangeArea}
                      />
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Этаж(ей):</label>
                    <div className="col-sm-9">
                      <input
                        type="text"
                        className="form-control"
                        id="inputPrice"
                        onChange={this.handleChangeFloor}
                      />
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Описание</label>
                    <div className="col-sm-9">
                      <textarea
                        className="form-control"
                        rows="3"
                        onChange={this.handleChangeDescription}
                        value={description}
                      />
                    </div>
                  </div>

                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Цена</label>
                    <div className="col-sm-9">
                      <input
                        type="number"
                        className="form-control"
                        id="inputPrice"
                        onChange={this.handleChangePrice}
                        value={price}
                      />
                    </div>
                  </div>
                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Email</label>
                    <div className="col-sm-9">
                      <input
                        type="email"
                        className="form-control"
                        id="inputPrice"
                        onChange={this.handleChangeEmail}
                        value={email}
                      />
                    </div>
                  </div>

                  <div className="form-group row mt-2">
                    <label className="col-sm-3 col-form-label">Телефон</label>
                    <div className="col-sm-9">
                      <InputMask
                        mask="+7 (999) 999-99-99"
                        value={tel}
                        onChange={this.handleChangeTel}
                        className="form-control"
                        id="phone"
                      />
                    </div>
                  </div>


                  <div className="form-group has-success">
                    <label className="control-label">
                      Показывать объвление всем
                    </label>
                    <Switch
                      onChange={this.handleToggle('statusTrue')}
                      checked={this.state.checked.indexOf('statusTrue') !== -1}
                    />
                  </div>
                  <div className="btn-group btn-block">
                    <button className="btn btn-outline-dark">
                      <Link to="/"
                        id="backLink"
                      >
                        Назад
                      </Link>
                    </button>

                    <button
                      type="submit"
                      className="btn btn-outline-success btn-block"
                    //  id="successAddItems"
                    >
                      Изменить
                    </button>

                    <button
                      type="button"
                      className="btn btn-outline-danger"
                      //id="Del_btn"
                      onClick={this.handleDellItem}
                    >
                      Удалить
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    item: getItemById(state, state.itemPage.id),
    categores: getCategories(state),
    token: getToken(state),
    files: getFiles(state),
    locations: getLocations(state),
    district: getDistrict(state),
    realtObject: getRealtObject(state),
  };
};

const mapDispatchToProps = {
  fetchItemByID,
  fetchCategories,
  patchonServer,
  delItem,
  uploadFile,
  DeleteImgFile,
  DeleteAllImgFile,
  fetchLocations,
  fetchDistrict,
  fetchRealtObject
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EditItem);
