import React from 'react';
import { connect } from 'react-redux';
import { getDistrict, getActiveDistrict } from './../../selectors';
import { changeActiveDistrict } from '../../actions';


class District extends React.PureComponent {
  handleChange = (e, i) => {
    let newDistrict = this.props.district.find(el => parseInt(el.id) === parseInt(e.target.id));
    if (!newDistrict) newDistrict = { id: undefined, name: "Все" }
    this.props.changeActiveDistrict(newDistrict)
  }

  render() {

    const { district, activeDistrict } = this.props;
    return (
      <div className="well">
        <span>Район</span>
        <div className="btn-group btn-block">
          <button type="button" className="btn btn-outline-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            {activeDistrict && activeDistrict.name}
          </button>
          <div className="dropdown-menu">
            <button className="dropdown-item" onClick={this.handleChange}>Все</button>
            {district.map((data, index) => <button className="dropdown-item" key={data.id} id={data.id} onClick={this.handleChange}>{data.name}</button>)}
          </div>
        </div>
      </div>
    );
  }
};

const mapStateToProps = (state) => ({
  district: getDistrict(state),
  activeDistrict: getActiveDistrict(state),
});

const initMapDispatchToProps = {
  changeActiveDistrict
};

export default connect(
  mapStateToProps,
  initMapDispatchToProps
)(District);
