import React from 'react';
import { connect } from 'react-redux';
import { getCategories, getActiveCategory } from './../../selectors';
import { changeActiveCategory } from '../../actions';


class Categories extends React.PureComponent {
  handleChange = (e, i) => {
    let newCategory = this.props.categories.find(el => parseInt(el.id) === parseInt(e.target.id));
    if (!newCategory) newCategory = { id: undefined, name: "Все" }
    this.props.changeActiveCategory(newCategory)
  }

  render() {

    const { categories, activeCategory } = this.props;
    return (
      <div className="well">
        <span>Категории</span>
        <div className="btn-group btn-block">
          <button type="button" className="btn btn-outline-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            {activeCategory && activeCategory.name}
          </button>
          <div className="dropdown-menu">
            <button className="dropdown-item" onClick={this.handleChange}>Все</button>
            {categories.map((category, index) => <button className="dropdown-item" key={category.id} id={category.id} onClick={this.handleChange}>{category.name}</button>)}
          </div>
        </div>
      </div>
    );
  }
};

const mapStateToProps = (state, ownProps) => ({
  categories: getCategories(state),
  activeCategory: getActiveCategory(state),
});

const initMapDispatchToProps = {
  changeActiveCategory
};

export default connect(
  mapStateToProps,
  initMapDispatchToProps
)(Categories);
