import { UPLOAD_FILE_SUCCESS, DELETE_IMG_FILE, DELETE_ALL_IMG_FILE} from './../actionTypes';

const initialState = [];

export default (state = initialState, { type, payload }) => {
	switch (type) {
    case UPLOAD_FILE_SUCCESS:
      return state.concat(payload);
    case DELETE_IMG_FILE:
    debugger
      return state.filter((el, i) => payload.indexOf(i) === -1);
    case DELETE_ALL_IMG_FILE:
      return initialState;
    default:
      return state;
  }
};
