export const fetchItems = () => async dispatch => {
  dispatch({ type: at.FETCH_ITEMS_START });

  try {
    const items = await fetchItemsApi();
    dispatch({
      type: at.FETCH_ITEMS_SUCCESS,
      payload: items,
    });
  } catch (err) {
    dispatch({
      type: at.FETCH_ITEMS_FAILURE,
      payload: err,
      error: true,
    });
  }
};

