export const loadMoreItems = () => async (dispatch, getState) => {
	const state = getState();
	const params = {};
  params.categoryId = state.activeCategory.id;
  params.locationId = state.activeLocation.id;
  params.districtId = state.activeDistrict.id;
  params.realtObjectId = state.activeRealtObject.id;
  params.deal = state.activeDeal;
	params.offset = getRenderedItemsLength(state, params.categoryId);
	params.priceRange = state.priceRange;
  dispatch({ type: at.LOAD_MORE_ITEMS_START });
  try {
    const items = await loadMoreItemsApi(params);
    dispatch({
      type: at.LOAD_MORE_ITEMS_SUCCESS,
      payload: items,
    });
  } catch (err) {
    dispatch({
      type: at.LOAD_MORE_ITEMS_FAILURE,
      payload: err,
      error: true,
    });
  }
};

