export const sendAdToServer = nitem => async dispatch => {
  dispatch({ type: at.UPLOAD_ITEMS_START });
  try {
    const res = await addItemApi(nitem);
    if (res.status === 201 || res.status === 200) {
      dispatch({ type: at.UPLOAD_ITEMS_SUCCESS });
      dispatch({
        type: at.POPUP_SHOW,
        payload: 'Объявление добавлено!',
      });
    } else {
      dispatch({
        type: at.UPLOAD_ITEMS_FAILURE,
        payload: res.data,
        error: true,
      });
      dispatch({
        type: at.POPUP_SHOW,
        payload: 'Ошибка!',
      });
    }
  } catch (err) {
    dispatch({
      type: at.UPLOAD_ITEMS_FAILURE,
      payload: err,
      error: true,
    });
  }
};

