import React, { Component } from 'react';
import { Link } from 'react-router';
import { connect } from 'react-redux';
import { getToken } from '../../selectors';
import { signOut } from './../../actions';
import PopUp from './../popup';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import './style.css';

class NavBar extends Component {
  render() {
    const { token, signOut, children } = this.props
    return (
      <div>
        <nav className="navbar fixed-top animated slideInDown pt-0 pb-0">
          <div className="container d-flex justify-content-center">
         
            <ul className="navbar-nav d-flex flex-row">
              <li className="nav-item ml-2 mr-2">
                <a className="nav-link" href="http://mir-zakona.ru/">
                  Главная
                </a>
              </li>
              <li className="nav-item ml-2 mr-2">
                <Link className="nav-link" to="/">
                  Объявления
                </Link>
              </li>

              {token && (
                <li className="nav-item ml-2 mr-2">
                  <Link className="nav-link" to="/admin">
                    {window.screen.width > 600
                      ? 'Администрирование'
                      : 'Админ'}
                  </Link>
                </li>
              )}

              {token ? (
                <li className="nav-item ml-2 mr-2">
                  <Link className="nav-link" to="/" onClick={signOut}>
                    Выйти
                  </Link>
                </li>
              ) : (
                <li className="nav-item ml-2 mr-2">
                  <Link className="nav-link" to="/signin">
                    войти
                  </Link>
                </li>
              )}
            </ul>
          </div>
        </nav>
        <div>
          <PopUp />
        </div>
        <div>{children}</div>
        {this.props.location.pathname !== '/additems' && (
          <div className="floatBtn animated fadeInUp">
            <Link to="/additems" className="add_new_items">
              <Fab color="primary" aria-label="Add">
                <AddIcon />
              </Fab>
            </Link>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    token: getToken(state),
  };
};

const mapDispatchToProps = {
  signOut,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(NavBar);
