import React from 'react';
import { connect } from 'react-redux';
import { getLocations, getActiveLocations } from './../../selectors';
import { changeActiveLocation } from '../../actions';


class Locations extends React.PureComponent {
  handleChange = (e, i) => {
    let newLocation = this.props.locations.find(el => parseInt(el.id) === parseInt(e.target.id));
    if (!newLocation) newLocation = { id: undefined, name: "Все" }
    this.props.changeActiveLocation(newLocation)
  }

  render() {

    const { locations, activeLocation } = this.props;
    return (
      <div className="well">
        <span>Населенный пункт</span>
        <div className="btn-group btn-block">
          <button type="button" className="btn btn-outline-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            {activeLocation && activeLocation.name}
          </button>
          <div className="dropdown-menu">
            <button className="dropdown-item" onClick={this.handleChange}>Все</button>
            {locations.map((data, index) => <button className="dropdown-item" key={data.id} id={data.id} onClick={this.handleChange}>{data.name}</button>)}
          </div>
        </div>
      </div>
    );
  }
};

const mapStateToProps = (state) => ({
  locations: getLocations(state),
  activeLocation: getActiveLocations(state),
});

const initMapDispatchToProps = {
  changeActiveLocation
};

export default connect(
  mapStateToProps,
  initMapDispatchToProps
)(Locations);
