import React from 'react';
import { connect } from 'react-redux';
import { getRealtObject, getActiveRealtObject } from './../../selectors';
import { changeActiveRealtObject } from '../../actions';


class RealtObject extends React.PureComponent {
  handleChange = (e, i) => {
    let newRealtObject = this.props.realtObject.find(el => parseInt(el.id) === parseInt(e.target.id));
    if (!newRealtObject) newRealtObject = { id: undefined, name: "Все" }
    this.props.changeActiveRealtObject(newRealtObject)
  }

  render() {

    const { realtObject, activerealtObject } = this.props;
    return (
      <div className="well">
        <span>Объект недвижимости</span>
        <div className="btn-group btn-block">
          <button type="button" className="btn btn-outline-success dropdown-toggle btn-block" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            {activerealtObject && activerealtObject.name}
          </button>
          <div className="dropdown-menu">
            <button className="dropdown-item" onClick={this.handleChange}>Все</button>
            {realtObject.map((data, index) => <button className="dropdown-item" key={data.id} id={data.id} onClick={this.handleChange}>{data.name}</button>)}
          </div>
        </div>
      </div>
    );
  }
};

const mapStateToProps = (state) => ({
  realtObject: getRealtObject(state),
  activerealtObject: getActiveRealtObject(state),
});

const initMapDispatchToProps = {
  changeActiveRealtObject
};

export default connect(
  mapStateToProps,
  initMapDispatchToProps
)(RealtObject);
