import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchItemByID } from './../../actions';
import { getOneItemById } from './../../selectors';
import { Sidebar } from './sidebar';
import Field from './fields';
import { getToken } from '../../selectors';
import {
  fetchCategories,
  fetchLocations,
  fetchDistrict,
  fetchRealtObject,
} from './../../actions';

class Item extends Component {
  componentDidMount() {
    this.props.fetchItemByID(this.props.params.id);
    this.props.fetchCategories();
    this.props.fetchLocations();
    this.props.fetchDistrict();
    this.props.fetchRealtObject();
  }

  render() {
    const { item } = this.props;

    if (item) {
      item.admin = false;
      item.phone = '+79898268018';

      if (this.props.token) {
        item.admin = true;
        item.phone = '+' + item.tel.replace(/[^\d]/g, '');
      }
    }

    return (
      <div className="container mt-5 pt-5">
        <div className="row ">
          <div className="col-md-12 col-lg-12">
            {item && <Field item={item} />}
          </div>
        </div>
        <div className="col-12 col-lg-12 p-0 mt-5">
          {item && <Sidebar item={item} />}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    item: getOneItemById(state, state.itemPage.id),
    token: getToken(state),
  };
};

const mapDispatchToProps = {
  fetchItemByID,
  fetchCategories,
  fetchLocations,
  fetchDistrict,
  fetchRealtObject,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Item);
