import React, { PureComponent } from 'react';

class PropsItem extends PureComponent {
  constructor(props) {
    super(props);
    this.onDelete = this.onDelete.bind(this);
  }

  onDelete() {
    const { onDelete, item } = this.props;

    if (typeof onDelete === 'function') {
      const id = item.id;
      onDelete(id);
    }
  }

  render() {
    const { item } = this.props;
		const ref = 'tel:+'+item.phone;
    return (
      <div class="card mb-2">
        <a href={ref} class="card-header">+{item.phone}</a>
        <div class="card-body">
          <p class="card-text">{item.text}</p>

          <button className="btn btn-outline-warning btn-small" onClick={this.onDelete}>
            Удалить
          </button>
        </div>
      </div>
    );
  }
}

export default PropsItem;
