import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import {
  getCategories,
  getFiles,
  getLocations,
  getDistrict,
  getRealtObject,
} from './../../selectors';
import {
  fetchCategories,
  sendAdToServer,
  uploadFile,
  DeleteImgFile,
  DeleteAllImgFile,
  fetchLocations,
  fetchDistrict,
  fetchRealtObject,
} from './../../actions';
import Gallery from 'react-grid-gallery';
import InputMask from 'react-input-mask';
import './style.css';
import { browserHistory } from 'react-router';
import ReCAPTCHA from 'react-google-recaptcha';

class AddItems extends Component {
  constructor(props) {
    super(props);
    this.state = {
      file: [],
      images: [],
      name: '',
      description: '',
      price: '',
      categoriesid: '',
      locations: '',
      district: '',
      realtObject: '',
      area: 0,
      floor: 0,
      sale: true,
      tel: '',
      email: '',
      robot: true,
      spinner: false,
    };
    this.sendAdToServer = this.props.sendAdToServer.bind(this);
    this.clickcapcha = this.clickcapcha.bind(this);
  }

  static getDerivedStateFromProps(props, state) {
    if (props.files.length !== state.images.length) {
      const img = props.files.map(src => {
        return {
          src: src,
          thumbnail: src,
          thumbnailWidth: 320,
          thumbnailHeight: 174,
          isSelected: false,
          caption: '',
        };
      });

      return { images: img, spinner: false };
    }
    return null;
  }

  componentDidMount() {
    this.props.fetchCategories();
    this.props.fetchLocations();
    this.props.fetchDistrict();
    this.props.fetchRealtObject();
  }

  clickcapcha = value => {
    if (value) {
      this.setState({ robot: false });
    }
  };

  allImagesSelected = images => {
    var f = images.filter(function(img) {
      return img.isSelected === true;
    });
    return f.length === images.length;
  };

  onSelectImage = index => {
    var images = this.state.images.slice();
    var img = images[index];
    if (img.hasOwnProperty('isSelected')) img.isSelected = !img.isSelected;
    else img.isSelected = true;

    this.setState({
      images: images,
    });

    if (this.allImagesSelected(images)) {
      this.setState({
        selectAllChecked: true,
      });
    } else {
      this.setState({
        selectAllChecked: false,
      });
    }
  };

  handleImageChange = e => {
    e.preventDefault();
    this.setState({ spinner: true });
    let reader = new FileReader();
    let file = e.target.files;
    for (let i = 0; i < file.length; i++) {
      if (file[i]) {
        reader.onloadend = () => {
          this.props.uploadFile(file[i]);
        };
        reader.readAsDataURL(file[i]);
      }
    }
  };

  getSelectedImages() {
    const selected = [];
    for (let i = 0; i < this.state.images.length; i++) {
      if (this.state.images[i].isSelected === true) selected.push(i);
    }
    return selected;
  }

  handleImageDell = () => {
    const seletIndex = this.getSelectedImages();
    this.props.DeleteImgFile(seletIndex);
  };

  handleChangeName = e => {
    this.setState({ name: e.target.value });
  };

  handleChangeDescription = e => {
    this.setState({ description: e.target.value });
  };

  handleChangePrice = e => {
    this.setState({ price: e.target.value });
  };

  handleChangeArea = e => {
    this.setState({ area: e.target.value });
  };

  handleChangeFloor = e => {
    this.setState({ floor: e.target.value });
  };

  handleChangeCategories = e => {
    this.setState({ categoriesid: e.target.value });
  };

  handleChangeLocation = e => {
    this.setState({ locations: e.target.value });
  };

  handleChangeDistrict = e => {
    this.setState({ district: e.target.value });
  };

  handleChangeRealtObject = e => {
    this.setState({ realtObject: e.target.value });
  };

  handleChangeEmail = e => {
    this.setState({ email: e.target.value });
  };

  handleToggleSale = () => {
    this.setState({ sale: !this.state.sale });
  };
  handleSubmit = e => {
    e.preventDefault();

    const {
      name,
      description,
      price,
      categoriesid,
      images,
      email,
      tel,
      robot,
      locations,
      district,
      realtObject,
      area,
      floor,
      sale,
    } = this.state;

    if (robot) {
      alert('мне кажется вы робот');
    } else {
      if (name && description) {
        const nItem = {
          name: name,
          description: description,
          price: price,
          categoryId:
            categoriesid !== '' ? categoriesid : this.props.categores[0].id,
          images: images.map(el => el.src),
          email: email,
          tel: tel,
          locationId:
            locations !== '' ? locations.id : this.props.locations[0].id,
          districtId: district !== '' ? district.id : this.props.district[0].id,
          realtObjectId:
            realtObject !== '' ? realtObject.id : this.props.realtObject[0].id,
          area: area,
          floor: floor,
          sale: sale,
        };
        this.sendAdToServer(nItem);
        this.props.DeleteAllImgFile();
        browserHistory.push('/');
      } else {
        alert('Не заполненны основные поля!');
      }
    }
  };
  handleChangeTel = e => {
    this.setState({ tel: e.target.value });
  };

  handleToggleSaleTrue = () => {
    this.setState({ sale: true });
  };
  handleToggleSaleFalse = () => {
    this.setState({ sale: false });
  };

  render() {
    const { images, spinner } = this.state;

    return (
      <div className="container mt-5 pt-5 text-center">
        <h1>Добавление нового объявления</h1>

        <div className="row add_item_container">
          <form className="col-lg-12" onSubmit={this.handleSubmit}>
            <div className="form-group row">
              <div className="col-md-6">
                <h5>Фото:</h5>
                <div className="btn-group">
                  <label className="btn btn-outline-success ">
                    Добавить
                    {spinner && (
                      <img
                        src="https://loading.io/spinners/rolling/lg.curve-bars-loading-indicator.gif"
                        id="spinner"
                        alt="..."
                      />
                    )}
                    <input
                      className="fileInput"
                      name="file"
                      type="file"
                      accept="image/*,image/jpeg"
                      onChange={this.handleImageChange}
                    />
                  </label>
                  <label className="btn btn-outline-warning">
                    Удавить выбранные
                    <input
                      className="fileDel"
                      type="button"
                      onClick={this.handleImageDell}
                    />
                  </label>
                </div>
                <div className="previewComponent">
                  {Array.isArray(images) && images.length !== 0 && (
                    <Gallery
                      images={images}
                      enableImageSelection={true}
                      onSelectImage={this.onSelectImage}
                    />
                  )}
                </div>
              </div>

              <div className="col-md-6 text-left">
                <h5>Заполните данными форму:</h5>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Название</label>
                  <div className="col-sm-9">
                    <input
                      type="text"
                      className="form-control"
                      onChange={this.handleChangeName}
                      id="inputName"
                    />
                  </div>
                </div>

                <div className="form-group row mt-2 d-flex justify-content-center">
                  <div className="form-group has-success ">
                    <div
                      className="btn-group btn-group-toggle"
                      data-toggle="buttons"
                    >
                      <label
                        className={
                          this.state.sale
                            ? 'btn btn-outline-secondary active'
                            : 'btn btn-outline-secondary'
                        }
                        onClick={this.handleToggleSaleTrue}
                      >
                        <input type="radio" name="options" id="option1" />{' '}
                        Продажа
                      </label>
                      <label
                        className={
                          this.state.sale
                            ? 'btn btn-outline-secondary'
                            : 'btn btn-outline-secondary active'
                        }
                        onClick={this.handleToggleSaleFalse}
                      >
                        <input type="radio" name="options" id="option2" />{' '}
                        Аренда
                      </label>
                    </div>
                  </div>
                </div>

                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">
                    Населенный пункт
                  </label>
                  <div className="col-sm-9">
                    <select
                      className="form-control"
                      onChange={this.handleChangeLocation}
                      value={this.state.locations}
                      id="select"
                    >
                      {this.props.locations.map(data => (
                        <option key={data.id} value={data.id}>
                          {data.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Район</label>
                  <div className="col-sm-9">
                    <select
                      className="form-control"
                      onChange={this.handleChangeDistrict}
                      value={this.state.district}
                      id="select"
                    >
                      {this.props.district.map(data => (
                        <option key={data.id} value={data.id}>
                          {data.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">
                    Объект недвижимости
                  </label>
                  <div className="col-sm-9">
                    <select
                      className="form-control"
                      onChange={this.handleChangeRealtObject}
                      value={this.state.realtObject}
                      id="select"
                    >
                      {this.props.realtObject.map(data => (
                        <option key={data.id} value={data.id}>
                          {data.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Категория</label>
                  <div className="col-sm-9">
                    <select
                      className="form-control"
                      onChange={this.handleChangeCategories}
                      value={this.state.categoriesid}
                      id="select"
                    >
                      {this.props.categores.map(data => (
                        <option key={data.id} value={data.id}>
                          {data.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Площадь:</label>
                  <div className="col-sm-9">
                    <input
                      type="number"
                      className="form-control"
                      id="inputPrice"
                      onChange={this.handleChangeArea}
                    />
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Этаж(ей):</label>
                  <div className="col-sm-9">
                    <input
                      type="text"
                      className="form-control"
                      id="inputPrice"
                      onChange={this.handleChangeFloor}
                    />
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Описание</label>
                  <div className="col-sm-9">
                    <textarea
                      className="form-control"
                      rows="3"
                      id="Description"
                      onChange={this.handleChangeDescription}
                    />
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Цена</label>
                  <div className="col-sm-9">
                    <input
                      type="number"
                      className="form-control"
                      id="inputPrice"
                      onChange={this.handleChangePrice}
                    />
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Email</label>
                  <div className="col-sm-9">
                    <input
                      type="email"
                      className="form-control"
                      id="inputEmail"
                      onChange={this.handleChangeEmail}
                    />
                  </div>
                </div>
                <div className="form-group row mt-2">
                  <label className="col-sm-3 col-form-label">Телефон</label>
                  <div className="col-sm-9">
                    <InputMask
                      mask="+7 (999) 999-99-99"
                      value={this.state.tel}
                      onChange={this.handleChangeTel}
                      className="form-control"
                      id="phone"
                    />
                  </div>
                </div>

                <div className="d-flex justify-content-center">
                  <ReCAPTCHA
                    sitekey="6Lel4pgUAAAAAP7gfcbJdyFfuJLssvgTHgvkPKmP"
                    onChange={this.clickcapcha}
                  />
                </div>

                <button
                  type="submit"
                  className="btn btn-success  btn-lg btn-block"
                  id="successAddItems"
                >
                  Добавить в базу
                </button>
                <Link
                  to="/"
                  id="backLink"
                  className="btn btn-inverse btn-block btn-lg"
                >
                  Назад
                </Link>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  categores: getCategories(state),
  locations: getLocations(state),
  district: getDistrict(state),
  realtObject: getRealtObject(state),
  files: getFiles(state),
});

const mapDispatchToProps = {
  sendAdToServer,
  fetchCategories,
  uploadFile,
  DeleteImgFile,
  DeleteAllImgFile,
  fetchLocations,
  fetchDistrict,
  fetchRealtObject,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AddItems);
